import cssString from "./css.js";

const textPart = document.querySelector(".textPart");
const cssPart = document.querySelector(".css");
const btnPause = document.querySelector(".btnPause");
const btnPlay = document.querySelector(".btnPlay");
const btnSlow = document.querySelector(".btnSlowSpeed");
const btnNormal = document.querySelector(".btnNormalSpeed");
const btnFast = document.querySelector(".btnFastSpeed");

let n = 0;
// è®¾ç½®æ’­æ”¾é€Ÿåº¦
let timerSpeed = 100;

// -------------------------------------------
// é¡µé¢çš„æ¸²æŸ“
let print = setInterval(() => {
  n = n + 1;
  if (n > cssString.length) {
    clearInterval(print);
    return;
  }

  // å½¢æˆé¡µé¢ä¸ŠåŠéƒ¨åˆ†çš„ä»£ç æ–‡å­—
  textPart.innerText = cssString.substring(0, n); // è¾“å‡ºä»ç¬¬0ä½åˆ°ç¬¬n-1ä½ï¼Œç¬¬nä½æ˜¯ä¸ä¼šè¾“å‡ºçš„

  // è®©é¡µé¢çš„ä¸ŠåŠéƒ¨åˆ†æ»šåŠ¨æœ€åº•éƒ¨ï¼ˆå…³é”®è¦åœ¨è¿™é‡Œé¢æ‰§è¡Œï¼‰
  textPart.scrollTop = textPart.scrollHeight;
  // textPart.scrollIntoView(false);
  // textPart.scrollTop = 9999999;

  // å½¢æˆé¡µé¢çš„ä¸‹åŠéƒ¨åˆ†çš®å¡ä¸˜
  cssPart.innerHTML = cssString.substring(0, n);
}, timerSpeed);

// -------------------------------------------
// printåŠŸèƒ½æ”¾å…¥å‡½æ•°ä¸­ï¼Œæ–¹ä¾¿åé¢å†æ¬¡è°ƒç”¨
let renderTextAndHtmlFn = function () {
  n = n + 1;

  console.log(n);
  if (n > cssString.length) {
    clearInterval(print);
    return;
  }
  textPart.innerText = cssString.substring(0, n);
  textPart.scrollTop = textPart.scrollHeight;
  cssPart.innerHTML = cssString.substring(0, n);
};

// -------------------------------------------
// å‡ ä¸ªåŠŸèƒ½é›†æˆåˆ°å¯¹è±¡ä¸­
const player = {
  runTimer() {
    // setIntervalä¼šæ‰§è¡Œå‡½æ•°ï¼Œä¹Ÿå°±æ˜¯ä¸‹é¢çš„renderTextAndHtmlFn
    return setInterval(renderTextAndHtmlFn, timerSpeed);
  },

  pauseTimer() {
    clearInterval(print);
  },

  pauseFeature() {
    // æ•´ä¸ªå¯¹è±¡ä¸­çš„pauseTimerå±æ€§
    player.pauseTimer();
  },

  playFeature() {
    player.pauseTimer();
    print = player.runTimer();
  },

  slowPlayFeature() {
    timerSpeed = 400;
    player.playFeature();
  },

  normalPlayFeature() {
    timerSpeed = 100;
    player.playFeature();
  },

  fastPlayFeature() {
    timerSpeed = 0;
    player.playFeature();
  },
};

